<template>
    <div>
        <div class="container text-center">
            <h1>Firebase Auth Test</h1>
            <div class="row justify-content-center mt-5">
                <div class="col-4">
                    <button type="button" class="btn btn-primary" @click="authPop">使用google註冊(Popup)</button>
                </div>
                <div class="col-4">
                    <button type="button" class="btn btn-primary" @click="authRedirect">使用google註冊(Redirect)</button>
                </div>
            </div>
            <div class="row justify-content-center mt-5">
                <div class="col-4">
                    <button type="button" class="btn btn-primary" @click="getCurrentUser">查詢當前使用者</button>
                </div>
                <div class="col-4">
                    <button type="button" class="btn btn-danger" @click="signOut">登出</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    // set provider
    var provider = new firebase.auth.GoogleAuthProvider();
export default {
    data() {
        return {

        }
    },
    methods: {
        authPop() {
            firebase.auth().signInWithPopup(provider).then(function(result) {
                // 可以獲得 Google 提供 token，token可透過 Google API 獲得其他數據。  
                var token = result.credential.accessToken;
                var user = result.user;
                console.log(token, user)
            }).catch(function(err) {
                console.log(err)
            })
        },
        authRedirect() {
            firebase.auth().signInWithRedirect(provider).then(function(result) {    
                var token = result.credential.accessToken;
                var user = result.user;
            }).catch(function(err) {
                console.log(err)
            })
        },
        getCurrentUser() {
            var user = firebase.auth().currentUser;
            if(user) {
                console.log(user.displayName)
            }else {
                console.log("未登入")
            }
            
        },
        signOut() {
            firebase.auth().signOut().then(function() {
                // Sign-out successful.
            }).catch(function(error) {
                // An error happened.
            });
        }
    },
}
</script>